import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";
const Navbar = () => {
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light shadow-lg  bg-light">
        <div className="container-fluid">
          <Link className="navbar-brand nav-logo" to="/">
            ArjavSethi
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto me-auto mb-2 mb-lg-0 "></ul>
            <ul class="navbar-nav ml-auto">
              {/* <li className="nav-item">
          <Link className="nav-link active" to="/contact">Contact</Link>
        </li> */}

              <li className="nav-item   active">
                <Link className="nav-link mx-3 active" to="/linktree">
                  Linktree
                </Link>
              </li>

              <li className="nav-item active">
                <Link className="nav-link mx-3 active" to="/projects">
                  Achievements
                </Link>
              </li>
              <li className="nav-item active">
                <Link className="nav-link mx-3 active" to="/Blog">
                  Blog
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
